<template>
  <div class="story-wrapper">
    <h1>{{title}}</h1>
    <div class="story" 
      v-for="(item, index) of content" 
      :key="index" 
      :style="{backgroundImage:'url(/static/images/npcimage/'+item.image+')'}"
    >
      <div class="stroy-content" >
        <div class="npc" v-if="item.npc">
          {{item.npc}}
        </div>
        <p class="question" v-html="item.question">
          {{item.question}}
        </p>
        <p class="answer" v-if="item.answer" v-html="item.answer">
          {{item.answer}}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StoryContent',
  props:{
    content: {
      required: true,
      type: Array
    },
    title: {
      required: true,
      type: String
    }
  }
}
</script>

<style lang="stylus" scoped>
h1 
  font-size: 1.5em;
  margin: 0 0 .5em 0;
  text-align: center;
.story-wrapper
  line-height: 20px; 
  .story
    background-color: #D2C6AC;
    background-repeat: no-repeat;
    background-position: right;
    border-radius: 10px;
    margin-top: 10px;
  .stroy-content
    width 70%;
    padding: 10px 0 1px 20px;
    .npc
      color: #14829B;
    .answer
      background:#F0EBE7;
</style>



