<template>
  <div class="chapter-wrapper">
    <!-- {{chapterId}} -->
    <!-- <el-menu
      @select="setContent"
    >
      <el-submenu index="chapter1">
        <template slot="title">
          <span>第一章：命运中的少女</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="1-1">选项1</el-menu-item>
          <el-menu-item index="1-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
     <el-submenu index="chapter1">
        <template slot="title">
          <span>第二章：追踪黑骑士</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="2-1">选项1</el-menu-item>
          <el-menu-item index="2-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu> -->
    <el-menu  @select="setContent" @open="getMenu" ref="mainmenu">
      <el-submenu v-for="item of chapterList" :key="item.id" :index="item.id">
        <template slot="title">
          <span>{{item.name}}</span>
        </template>
        <el-menu-item-group>
          <el-menu-item 
            v-for="childMenu of subMenuList[item.id]" 
            :key="childMenu.id" 
            :index="childMenu.id"
           >
            {{childMenu.name}}
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'ChapterList',
  props: {
    hero: String,
    subMenuList: Object,
    chapter: String,
    chapterList: Array,
    chooseSub: Boolean
  },
  updated() {
    if(!this.chooseSub) {
      this.$refs["mainmenu"].close(this.chapter)
    }
  },
  methods: {
    setContent(key, keyPath) {
      console.log(key);
      this.$emit("select", key)
    },
    getMenu(key, keyPath) {
      this.$emit("getMenu", key)
    }
  }
}
</script>

<style lang="stylus" scoped>
  
</style>



