<template>
    <div class="main-wrapper">
        <div class="header-wrapper">
            <el-container>
                <el-header>
                    <player-role @rolechange="handleRole"></player-role>
                </el-header>
            </el-container>
        </div>
        <div class="main">
            <el-container class="main-content">
                <el-aside class="aside-wrapper" style="width: 20%;min-width: 200px">
                    <chapter-list 
                        :chooseSub="chooseSub"
                        :hero="hero" 
                        :chapter = "chapter"
                        :chapterList = "chapterList"
                        :subMenuList = "subMenuList"
                        @select="getContent"
                        @getMenu = "getMenu"
                        ></chapter-list>
                </el-aside>
                <el-main class="plot-wrapper">
                    <story-content :title="title" :content="content" ></story-content>
                </el-main>
            </el-container>
        </div>
    </div>
</template>

<script>
import PlayerRole from "common/header/PlayerRole.vue";
import ChapterList from "./components/ChapterList.vue";
import StoryContent from "./components/StoryContent.vue";
import axios from "axios";
export default {
  name: "Main",
  components: {
    PlayerRole,
    ChapterList,
    StoryContent
  },
  data() {
    return {
      hero: "warrior",
      title: "",
      chooseSub: true,
      content: [],
     // menuData: [],
      subMenuList: {
        chapter1: [],
        chapter2: [],
        chapter3: [],
        chapter4: [],
        chapter5: [],
        chapter6: [],
        chapter7: [],
        chapter8: [],
        chapter9: [],
        chapter10: [],
        chapter11: [],
        chapter12: [],
        chapter13: [],
        chapter14: [],
        chapter15: [],
        chapter16: [],
        chapter17: [],
        chapter18: []
      },
      chapter: "",
      chapterList: []
    };
  },
  methods: {
    handleRole(e) {
      //接受角色id，传递给章节列表
      if(this.hero != e) {
        this.hero = e;
        this.getChapter();
      }
      this.chooseSub = false;
    },
    getContent(index) {
      axios
        .get("/static/data/json/content/mainquest/" + this.hero + "/" + index + ".json")
        .then(this.setContent)
        .catch((error) => {
          this.setWarrior(index);
        });
    },
    setWarrior(index) {
       axios
        .get("/static/data/json/content/mainquest/warrior/" + index + ".json")
        .then(this.setContent)
    },
    setContent(res) {
      document.querySelector(".plot-wrapper").scrollTop = 0
      res = res.data;
      this.title = res.qkname;
      this.content = res.data;
    },
    getMenu(key, keyPath) {
      this.chooseSub = true;
      this.chapter = key;
      this.setMenu(key);
    },
    setMenu(key) {
      axios
        .get("/static/data/json/menu/" + this.hero + "/" + key + ".json")
        .then(this.setSubmenu)
        .catch((error) => {
          this.setError(key);
        });
    },
    setError(msg) {
      axios
        .get("/static/data/json/menu/warrior/" + msg + ".json")
        .then(this.setSubmenu);
    },
    setSubmenu(res) {
      res = res.data;
      if (res) {
        this.subMenuList[this.chapter] = res;
      }
    },
    getChapter() {
      axios.get("/static/data/json/chapter.json").then(this.setChapter);
    },
    setChapter(res) {
      res = res.data;
      if (res) {
        this.chapterList = res.data;
      }
    }
  },
  mounted() {
    this.getChapter();
  }
};
</script>

<style lang="stylus" scoped>
.main-wrapper >>> .el-container {
    height: 60px;
    top: 0;
    left: 0;
    width: 100%;
    line-height: 60px;
    z-index: 100;
    position: relative;
}

.main-wrapper {
    height: 100%;

    .header-wrapper {
        position: fixed;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 101;
    }

    .main {
        height: 100%;

        .plot-wrapper, .aside-wrapper {
            margin-top: 60px;
            width: 20%;
            min-width: 200px;
        }

        .main-content {
            height: 100%;
            max-width: 1160px;
            margin: 0 auto;
        }
    }
}
</style>



