<template>
  <div class="wrapper">
    <h1 class="title">
        <a href="/" class="el-menu-item">Dragon Nest</a>
    </h1>
    <el-menu ref="mainmenu"  default-active="warrior" class="el-menu-demo nav" mode="horizontal" @select="locateRole">
      <el-menu-item :index="item.id" v-for="item of roles" :key="item.id">{{item.name}}</el-menu-item>
    </el-menu>
    <div class="line"></div>
  </div>
</template>

<script>
import axios from "axios" //ajax
export default {
  name: "PlayerRole",
  props: {
    chapter: String
  },
  data() {
    return {
      roles: []
    };
  },
  methods: {
    getRoles() {
      axios.get("/static/data/json/role.json").then(this.setRoles);
    },
    setRoles(res) {
      let roleData = res.data;
      if (roleData.ret && roleData.data) {
        this.roles = roleData.data.roleList;
      }
    },
    locateRole(key, keyPatch) {
     //点击角色，传递角色id给父组件
      this.$emit("rolechange", key)
    }
  },
  mounted() {
    this.getRoles();
  }
};
</script>

<style lang="stylus" scoped>

.wrapper
    max-width: 1140px;
    margin: 0 auto;
    .title
      margin: 0;
      float: left;
      width: 20%;
      min-width: 200px;
      .el-menu-item:hover
        background-color: transparent;
    .nav
      float: left;
      height: 100%;
      line-height: 60px;
      background: transparent;
      padding: 0;
      .el-menu-item
        padding: 0 30px
</style>



